@import url('https://fonts.googleapis.com/css2?family=Urbanist:wght@200;300;400;500;600;700&display=swap');

/* Global Styles */
:root {
  --primary-color: #3b89fd;
  --primary-color-900: #023a8f;
  --primary-color-800: #014bb9;
  --primary-color-600: #68a5ff;
  --primary-color-400: #8ebbff;
  --dark-color: #333;
  --light-color: #e2e2e2;
  --darker-color: #8b8b8b;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Urbanist', sans-serif;
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
}

a {
  color: rgb(0, 0, 0);
  text-decoration: none;
}

a:hover {
  color: rgba(255, 255, 255, 0.84);
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

/* Utility */

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--primary-color);
  color: #fff;
}

/* Margin */
.m {
  margin: 0.5rem;
}

.m-1 {
  margin: 1rem;
}

.m-2 {
  margin: 2rem;
}

.m-3 {
  margin: 3rem;
}

.my {
  margin: 0.5rem 0;
}

.my-1 {
  margin: 1rem 0;
}

.my-2 {
  margin: 2rem 0;
}

.my-3 {
  margin: 3rem 0;
}

.mx {
  margin: 0 0.5rem;
}

.mx-1 {
  margin: 0 1rem;
}

.mx-2 {
  margin: 0 2rem;
}

.mx-3 {
  margin: 0 3rem;
}

/* List */
.list {
  margin: 0.5rem 0;
}

.list li {
  padding-bottom: 0.3rem;
}

/* Padding */
.p {
  padding: 0.5rem;
}

.p-1 {
  padding: 1rem;
}

.p-2 {
  padding: 2rem;
}

.p-3 {
  padding: 3rem;
}

.py {
  padding: 0.5rem 0;
}

.py-1 {
  padding: 1rem 0;
}

.py-2 {
  padding: 2rem 0;
}

.py-3 {
  padding: 3rem 0;
}

.px {
  padding: 0 0.5rem;
}

.px-1 {
  padding: 0 1rem;
}

.px-2 {
  padding: 0 2rem;
}

.px-3 {
  padding: 0 3rem;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.4rem 0.7rem;
  z-index: 1;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: var(--primary-color);
  color: #fff;
}

.navbar h5 {
  font-weight: 400;
}

.navbar .brand-logo h5 {
  font-size: 1rem;
}

.logo {
  display: flex;
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: rgb(0, 0, 0);
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: var(--light-color);
}

.card-header span {
  font-size: 1rem;
  font-weight: 400;
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

/* Main Page Item */

.main-page-item {
  padding: 0.5rem;
  background-color: var(--primary-color-400);
  justify-content: center;
}

.main-page-item:nth-child(4n+1),
.main-page-item:nth-child(4n+4) {
  background-color: var(--primary-color-600);
}

.card {
  position: relative;
}

.card .forward-arrow {
  position: absolute;
  right: 0;
}

.main-page-item .image {
  width: 85%;
  height: 85px;
}

.main-page-item .card-body {
  flex-grow: 1;
  text-align: end;
}

.list-header {
  background-color: var(--primary-color-900);
  color: #fff;
  font-size: 0.8rem;
}

.main-card {
  display: flex;
  align-items: center;
  min-height: 200px;
  background-color: var(--primary-color-600);
  margin-top: 3rem;
}

.main-card-image {
  max-width: 50%;
}

.main-card-info {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.main-card-info h4 {
  font-size: 2em;
  font-weight: 200;
}

.main-card-info .label {
  text-transform: uppercase;
  font-size: 0.6rem;
  font-weight: 400;
  margin: -5px 0;
}

.main-card-info .value {
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 400;
}

.section {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

/* List */
.list-card {
  margin-top: 3rem;
}

.card-header {
  height: 10rem;
  display: flex;
  align-items: center;
  padding: 0 2rem;
  background-color: var(--primary-color-600);
}

.card-header h2 {
  font-size: 1.5rem;
  padding: 0 0.3rem;
  color: black;
  line-height: 1;
}

.card-header img {
  height: 120px;
  width: auto;
}

.list-card .list-item {
  padding: 0 0.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  background-color: var(--primary-color-400);
}

.list-card .list-item:nth-child(2n+1) {
  background-color: var(--primary-color-600);
}

.list-card .list-item p {
  font-size: 0.8rem;
  font-weight: 600;
}

.list-card .list-item > div {
  height: 3rem;
  font-size: 1.5rem;
  font-weight: 200;
}
